import React, { useState } from "react";
import styled from "styled-components";
import Img1 from "assets/Image/card1.png";
import Img2 from "assets/Image/card2.png";
import Img3 from "assets/Image/card3.png";

const CardSetContainer = styled.div`
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
`;
const CardSet = styled.div`
	display: flex;
	width: 50vw;
	height: 18vw;
	border-radius: 1.7vw;
	background-color: ${(props) => props.theme.textColor};
	overflow: hidden;
	margin: 30px 0px;
	box-sizing: border-box;
	@media only screen and (max-width: 480px) {
		height: 28.8vw;
		width: 80vw;
		margin: 8px 0px;
	}
	@media only screen and (min-width: 480px) and (max-width: 768px) {
		height: 28.8vw;
		width: 80vw;
		margin: 8px 0px;
	}
	@media only screen and (min-width: 768px) and (max-width: 1200px) {
		height: 21.6vw;
		width: 60vw;
		margin: 8px 0px;
	}
`;

const Card = styled.img`
	background-color: aliceblue;
	width: 25vw;
	height: 18vw;
	z-index: 2;
	box-sizing: border-box;
	@media only screen and (max-width: 480px) {
		height: 28.8vw;
		width: 80vw;
	}
	@media only screen and (min-width: 480px) and (max-width: 768px) {
		height: 28.8vw;
		width: 40vw;
	}
	@media only screen and (min-width: 768px) and (max-width: 1200px) {
		height: 21.6vw;
		width: 30vw;
	}
`;

const KeyWord = styled.strong`
	display: block;
	color: ${(props) => props.theme.pointColor};
	font-size: 0.7vw;
	@media only screen and (max-width: 480px) {
		font-size: 6px;
	}
`;
const Title = styled.strong`
	display: block;
	color: ${(props) => props.theme.bgColor};
	font-size: 1.5vw;
	letter-spacing: -2px;
	margin-bottom: 8px;
	@media only screen and (max-width: 480px) {
		font-size: 12px;
	}
`;
const Discription = styled.div`
	color: ${(props) => props.theme.bgColor};
	font-size: 0.9vw;
	letter-spacing: -1px;
	@media only screen and (max-width: 480px) {
		display: none;
	}
`;
const TextWrapper = styled.div`
	padding: 3.6vw 0 0 2.6vw;
	background-color: white;
	width: 25vw;
	height: 18vw;
	@media only screen and (max-width: 480px) {
		height: 28.8vw;
		width: 80vw;
		padding: 8vw 0 0 2.6vw;
	}
	@media only screen and (min-width: 480px) and (max-width: 768px) {
		height: 28.8vw;
		width: 40vw;
	}
	@media only screen and (min-width: 768px) and (max-width: 1200px) {
		height: 21.6vw;
		width: 30vw;
	}
`;

const Point = styled.span`
	color: ${(props) => props.theme.pointColor};
`;

const TitleText = styled.strong`
	color: ${(props) => props.theme.textColor};
	font-size: 2vw;
	margin: 160px 0px;
	@media only screen and (max-width: 480px) {
		font-size: 18px;
		margin: 12px 0px;
	}
	@media only screen and (min-width: 480px) and (max-width: 768px) {
		margin: 0px;
	}
	@media only screen and (min-width: 768px) and (max-width: 1200px) {
		margin: 60px 0px;
	}
`;
const Content = [
	{
		keyword: ["# ", "# ", "# "],
		title: ["  ", <br />, "   ."],
		discription: [
			"  ,  ",
			<br />,
			" , ,   ",
			<br />,
			"     ",
			<br />,
			"    .",
		],
	},
	{
		keyword: ["# ", "# "],
		title: [" ", <br />, "   ."],
		discription: [
			"      ",
			<br />,
			"      ",
			<br />,
			"      ",
			<br />,
			"    .",
		],
	},
	{
		keyword: ["# ", "# "],
		title: [" ", <br />, " ."],
		discription: [
			", ,  3     ",
			<br />,
			"  ,   ",
			<br />,
			"  .     ",
			<br />,
			"   .",
		],
	},
];

const JustCards = () => {
	return (
		<CardSetContainer>
			<TitleText>
				<Point></Point>     ?
			</TitleText>
			<CardSet>
				<Card src={Img1}></Card>
				<TextWrapper>
					<KeyWord>{Content[0].keyword}</KeyWord>
					<Title>{Content[0].title}</Title>
					<Discription>{Content[0].discription}</Discription>
				</TextWrapper>
			</CardSet>
			<CardSet>
				<TextWrapper>
					<KeyWord>{Content[1].keyword}</KeyWord>
					<Title>{Content[1].title}</Title>
					<Discription>{Content[1].discription}</Discription>
				</TextWrapper>
				<Card src={Img2}></Card>
			</CardSet>
			<CardSet>
				<Card src={Img3}></Card>
				<TextWrapper>
					<KeyWord>{Content[2].keyword}</KeyWord>
					<Title>{Content[2].title}</Title>
					<Discription>{Content[2].discription}</Discription>
				</TextWrapper>
			</CardSet>
			{/* <CardCover></CardCover> */}
		</CardSetContainer>
	);
};

export default JustCards;
