import React, { useState, useEffect, useRef } from "react";
import styled from "styled-components";
import Cursor from "./components/cursor/Cursor";
import Landing from "./pages/Landing";
import NavBar from "./pages/NavBar";
import Footer from "./pages/Footer";
import Header from "./pages/Header";
import HeaderBack from "./pages/HeaderBack";
import Slogan from "./components/main/Slogan";
import Mumo from "./components/main/Mumo";
import MovingLabel from "components/main/MovingLabel";
import JustCards from "pages/JustCards";
import QnA from "pages/QnA";
import Members from "pages/Members";
import Sponser from "pages/Sponser";
import Youtube from "pages/Youtube";

const App = () => {
	const [isReady, setIsReady] = useState(false);

	useEffect(() => {
		setIsReady(false);
		setTimeout(() => {
			setIsReady(true);
		}, 3000); //  3000
	}, []);

	const AppStyle = styled.div`
		background-color: ${(props) => props.theme.bgColor};
		width: 100vw;
		min-height: 100vh;
		height: auto;
		color: ${(props) => props.theme.textColor};
		text-overflow: hidden;
	`;

	return (
		<AppStyle>
			<Cursor />
			{!isReady && <Landing />}
			{isReady && (
				<>
					<HeaderBack />
					<NavBar />
					<Header />
					<Slogan />
					<Mumo />
					<MovingLabel />
					<JustCards />
					<Members />
					<Youtube />
					<MovingLabel />
					<QnA />
					<Sponser />
					<Footer />
				</>
			)}
		</AppStyle>
	);
};

export default App;
